.section_search {
  margin-top: 0.8vh;
  width: 60vh;
  height: 45px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: white;
  border-radius: 2px;
  box-shadow: 1px 1px 10px 1px rgb(189, 181, 39);
}
.search {
  width: 55vh;
  height: 45px;
  border: none;
}

@media (max-width: 1000px){
 
      .section_search{
        width: 30vh;
      }
      .search{
        width: 30vh;
      }
     
}

@media (max-width: 740px){

      .section_search{
        width: auto;
      }
      .search{
       width: auto;
      }
}
